export default {
    name: 'OrderRow',
    data () {
        return {
            state: 'online'
        }
    },
    props: ['item',],
    computed: {
        maxRate: function () {
            return Number(this.item.max_amount).toFixed(2)
        },
        minRate: function(){
            return Number(this.item.min_amount).toFixed(2)
        },
        profit: function(){
            return Number(this.item.profit).toFixed(2)
        },
        rate: function(){
            return Number(this.item.rate).toFixed(2)
        }
    },
    template: `
        <div class="i-tab__row">
          <div class="i-tab__cell online">
            <p class="i-tab__prod-name">{{item.user.username}}<span class="dot"></span></p>
            <div class="i-tab__rat">
              <div class="rat">
                <div class="rat__str"></div>
                <div class="rat__line">
                  <div class="rat__inner abs"></div>
                 </div>
              </div>
            </div>
          </div>
           
          <div class="i-tab__cell">
            
            <p v-for="processor in item.processors" class="i-tab__payment">{{processor.name}}</p>
          </div>
          
          <div class="i-tab__cell">
            <p class="i-tab__price-usd">{{rate}}&nbsp<span>{{item.currency.name}}</span></p>
            <p class="i-tab__price-hint"></p>
            <p class="i-tab__price-markup">{{profit}}%</p>
          </div>
          
          <div class="i-tab__cell">
            <p class="i-tab__limit">{{minRate}} - {{maxRate}} USD</p>
          </div>
          
          <div class="i-tab__cell">
            <div class="i-tab__buy"><a class="l-btn l-btn--secondary i-tab__buy-btn" href="#"></a></div>
          </div>
        </div>
    `
}


