import axios from 'axios'
import PerfectScrollbar from 'perfect-scrollbar'

import switchState from './switchState'
import AppSelect from './AppSelect'
import OrderRow from './OrderRow'
import AppLoader from './AppLoader'



export default Vue.component('OrdersApp',{
    name: 'OrdersApp',
    data () {
        return {
            transactionType: 'buy',
            dealType: 'online',
            data: [],
            info: null,
            loading: true,
            errored: false,
            page: 1,
            limit: 5,
        }
    },
    components: {
        switchState: switchState,
        AppSelect: AppSelect,
        OrderRow: OrderRow,
        AppLoader: AppLoader

    },
    template: `
        <section class="orders">
            <div class="l-container l-container--full-600">
              <div class="l-container-center">
                  <div class="l-inner l-inner--10">
                    <div class="orders__inner">
                      <div class="orders__h-control">
                        <div class="orders__h-control-switch">
                        
                        <!--SwitchState componnet-->
                          <switch-state v-on:switchState="dealType = $event"></switch-state>
                          
                        </div>
                        <div class="orders__h-control-country">
                          <app-select></app-select>
                          
                        </div>
                      </div>
                      <div class="info-tab__table">
                        <div class="card-tab">
                            <div id="js-orderApp-card-tab__tabs-wrap">
                                <div class="card-tab__tabs">
        
                                  <div 
                                    class="card-tab__tab"
                                    v-bind:class="{'card-tab__tab--active': transactionType === 'buy'}"
                                    v-on:click="changeTransactionType('buy')">
                                    <span> </span>
                                  </div>
                                  <div 
                                    class="card-tab__tab"
                                    v-bind:class="{'card-tab__tab--active': transactionType === 'sell'}"
                                    v-on:click="changeTransactionType('sell')">
                                    <span> </span>
                                  </div>
                                  <div 
                                    class="card-tab__tab"
                                    v-bind:class="{'card-tab__tab--active': transactionType === 'take-credit'}"
                                    v-on:click="changeTransactionType('take-credit')">
                                    <span>    </span>
                                  </div>
                                  <div 
                                    class="card-tab__tab"
                                    v-bind:class="{'card-tab__tab--active': transactionType === 'credit'}"
                                    v-on:click="changeTransactionType('credit')">
                                    <span>   </span>
                                  </div>
                                </div>
                              </div>
                          </div>
                          
                          <div class="card-tab__inner card-tab__inner--nopaddig-600">
                            
                            <div class="card-tab__content">
        
                              <!--i-tab__head-->
                              <div class="i-tab__head i-tab__row">
                                <div class="i-tab__head-cell i-tab__cell"><span></span></div>
                                <div class="i-tab__head-cell i-tab__cell"><span> </span></div>
                                <div class="i-tab__head-cell i-tab__head-cell--sort i-tab__head-cell--up i-tab__cell"><span> /BTC</span></div>
                                <div class="i-tab__head-cell i-tab__cell"><span></span></div>
                              </div>
    
                              <!--/end i-tab__head-->
                              <div class="i-tab__loader" v-if="loading">
                                <app-loader></app-loader>
                              </div>
                              <div class="i-tab__not-found" v-if="!data.length && !loading">
                                <span>  </span>                            
                              </div>
                              
                              <!--i-tab__row-->
                              <div v-for="item in data">
                                <order-row key="item.id" :item="item"></order-row>
                              </div>
    
                            </div>
                            
                            <div class="i-tab__footer">
                                <button class="l-btn l-btn--arr" v-on:click="page++">
                                  <div class="l-btn__inner">
                                    <img src="img/icons/reload-more.png"/><span> </span>
                                  </div>
                                </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
            </div>
      </section>
    `,
    computed: {
        category: function(){
            if (this.dealType === 'online') {
                switch (this.transactionType) {
                    case 'buy':
                        return 4
                        break
                    case 'sell':
                        return 3
                        break
                    case 'take-credit':
                        return 5
                        break
                    case 'credit':
                        return 6
                        break
                }
            } else if (this.dealType === 'offline'){
                switch (this.transactionType){
                    case 'buy':
                        return 2
                        break
                    case 'sell':
                        return 1
                        break
                    case 'take-credit':
                        return 7
                        break
                    case 'credit':
                        return 8
                        break
                }
            }
        },
        // page: function(){
        //     return 1
        // }
    },
    mounted() {
        this.getData();
        this.initializeScrollBar();

    },
    methods: {
        getData: function(){
            this.loading = true;
            this.data = [];
            axios
                .get(`http://front.bigrate.loc/api/orders?category=${this.category}&country=0&page=1&limit=${this.limit}`)
                .then(response => {
                    this.data = response.data.items;
                    // console.log(response)
                    // console.log(this.data)
                })
                .catch(error => {
                    // console.log(error);
                    this.errored = true;
                })
                .finally(() => (this.loading = false));
        },
        getMoreData: function(){
            this.loading = true;
            axios
                .get(`http://front.bigrate.loc/api/orders?category=${this.category}&country=0&page=${this.page}&limit=${this.limit}`)
                .then(response => {
                    this.data = [...this.data].concat(response.data.items);
                    // console.log(response)
                    // console.log(this.data)
                })
                .catch(error => {
                    // console.log(error);
                    this.errored = true;
                })
                .finally(() => (this.loading = false));
        },
        changeTransactionType: function(data){
            this.transactionType = data;
        },
        initializeScrollBar: function () {
            // const wrapScroll = get
            const ps = new PerfectScrollbar('#js-orderApp-card-tab__tabs-wrap', {
                suppressScrollX: false,
                suppressScrollY: true,
            });
        }
    },
    watch: {
        category: function(){
            this.getData()
        },
        page: function(){
            // // console.log('page was changed')
            this.getMoreData()
        }
    }
});


